<template>
  <el-card class="auth-add">

<!-- 
    <el-table-column  prop="id"  label="ID"  width="180"></el-table-column>
    <el-table-column  prop="fid"  label=""></el-table-column>
    <el-table-column  prop="uri"  label="URI"></el-table-column>
    <el-table-column  prop="route"  label=""></el-table-column>
    <el-table-column  prop="authRole"  label=""></el-table-column>
    <el-table-column  prop="operUser"  label="UserAccount"></el-table-column>
    <el-table-column  prop="checkLogin"  label=""></el-table-column>
    <el-table-column  prop="isDef"  label=""></el-table-column>
    <el-table-column  prop="isOpen"  label=""></el-table-column>-->
    <el-header>
      
    </el-header>
    <el-form :inline="true" :model="searchForm" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="searchForm.serviceName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.method" placeholder="">
          <el-option label="GET" value="GET"></el-option>
          <el-option label="POST" value="POST"></el-option>
          <el-option label="PUT" value="PUT"></el-option>
          <el-option label="DELETE" value="DELETE"></el-option>
          <el-option label="PATCH" value="PATCH"></el-option>
          <el-option label="HEAD" value="HEAD"></el-option>
          <el-option label="OPTIONS" value="OPTIONS"></el-option>
          <el-option label="TRACE" value="TRACE"></el-option>
          <el-option label="CONNECT" value="CONNECT"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="URI">
        <el-input v-model="searchForm.uri" placeholder="URI"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.route" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
<!--      authRoleel-tag-->
        <el-input v-model="currentAnyRole" placeholder="" @keyup.native.enter="addTag(0)" @keydown.native.delete="delTag(0)">
        </el-input>
        <el-tag
            v-for="tag in searchForm.anyRole"
            :key="tag"
            closable
            @close="handleCloseTag(item,0)"
            :disable-transitions="false">{{tag}}</el-tag>
      </el-form-item>
      <el-form-item label="">
        <!--      authRoleel-tag-->
        <el-input v-model="currentMustRole" placeholder="" @keyup.native.enter="addTag(2)" @keydown.native.delete="delTag(2)">
        </el-input>
        <el-tag
            v-for="tag in searchForm.mustRole"
            :key="tag"
            closable
            @close="handleCloseTag(item,2)"
            :disable-transitions="false">{{tag}}</el-tag>
      </el-form-item>
      <el-form-item label="">
        <!--      authRoleel-tag-->
        <el-input v-model="currentAnyPermission" placeholder="" @keyup.native.enter="addTag(1)" @keydown.native.delete="delTag(1)">
        </el-input>
        <el-tag
            v-for="tag in searchForm.anyPermission"
            :key="tag"
            closable
            @close="handleCloseTag(item,1)"
            :disable-transitions="false">{{tag}}</el-tag>
      </el-form-item>
      <el-form-item label="">
        <!--      authRoleel-tag-->
        <el-input v-model="currentMustPermission" placeholder="" @keyup.native.enter="addTag(3)" @keydown.native.delete="delTag(3)">
        </el-input>
        <el-tag
            v-for="tag in searchForm.mustPermission"
            :key="tag"
            closable
            @close="handleCloseTag(item,3)"
            :disable-transitions="false">{{tag}}</el-tag>
      </el-form-item>
      <el-form-item label="">
        <!--      authRoleel-tag-->
        <el-input v-model="currentLogicIndex" placeholder="" @keyup.native.enter="addTag(4)" @keydown.native.delete="delTag(4)" type="number">
        </el-input>
        <el-tag
            v-for="tag in searchForm.logicIndex"
            :key="tag"
            closable
            @close="handleCloseTag(item,4)"
            :disable-transitions="false">{{tag}}</el-tag>
      </el-form-item>
      <el-form-item label="anymust">
        <el-select v-model="searchForm.roleMode" placeholder="">
          <el-option label="OR" value=0></el-option>
          <el-option label="AND" value=1></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="anymust">
        <el-select v-model="searchForm.permissionMode" placeholder="">
          <el-option label="OR" value=0></el-option>
          <el-option label="AND" value=1></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="UserAccount">
        <el-input v-model="searchForm.operUser" placeholder="UserAccount"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.checkLogin" placeholder="">
          <el-option label="" value="true"></el-option>
          <el-option label="" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.isDef" placeholder="">
          <el-option label="" value="true"></el-option>
          <el-option label="" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.isOpen" placeholder="">
          <el-option label="" value="true"></el-option>
          <el-option label="" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-button
          type="primary"
          icon="el-icon-plus"
          @click="search"
      ></el-button>
    </el-form>
  </el-card>
</template>

<script>
import {AuthApi} from "@/core/request/api/AuthApi";

export default {
  name: "auth-add",
  data(){
    return {
      currentAnyRole:null,
      currentAnyPermission:null,
      currentMustRole:null,
      currentMustPermission:null,
      currentLogicIndex:0,
      searchForm: {
        uri: '',
        serviceName: '',
        route: '',
        anyRole: [],
        mustRole:[],
        anyPermission: [],
        mustPermission: [],
        logicIndex:[0],
        roleMode:null,
        permissionMode:null,
        operUser: '',
        checkLogin: '',
        isDef: '',
        isOpen: '',
        current:1,
        pageSize:10
      }
    }
  },
  methods: {
    handleCloseTag(item,t=0){
      switch (t){
        case 0:
          return this.searchForm.anyRole.splice(this.searchForm.authRole.indexOf(item), 1);
        case 1:
          return this.searchForm.anyPermission.splice(this.searchForm.anyPermission.indexOf(item), 1)
        case 2:
          return this.searchForm.mustRole.splice(this.searchForm.mustRole.indexOf(item), 1)
        case 3:
          return this.searchForm.mustPermission.splice(this.searchForm.mustPermission.indexOf(item), 1)
        case 4:
          return this.searchForm.logicIndex.splice(this.searchForm.logicIndex.indexOf(item), 1)
      }
    },
    addTag(t=0){
      switch (t){
        case 0:
          if (this.currentAnyRole!=null&&this.currentAnyRole!='')this.searchForm.anyRole.push(this.currentAnyRole);
          return this.currentAnyRole = null;
        case 1:
          if (this.currentAnyPermission!=null&&this.currentAnyPermission!='') this.searchForm.anyPermission.push(this.currentAnyPermission);
          return this.currentAnyPermission = null;
        case 2:
          if (this.currentMustRole!=null&&this.currentMustRole!='') this.searchForm.mustRole.push(this.currentMustRole);
          return this.currentMustRole = null;
        case 3:
          if (this.currentMustPermission!=null&&this.currentMustPermission!='') this.searchForm.mustPermission.push(this.currentMustPermission);
          return this.currentMustPermission = null;
        case 4:
          if (this.currentLogicIndex!=null&&this.currentLogicIndex!='') this.searchForm.logicIndex.push(this.currentLogicIndex);
      }

    },
    delTag(t=0){
      switch (t){
        case 0:
          if (this.currentAnyRole==null ||  this.currentAnyRole==''){
            return this.searchForm.anyRole.splice(this.searchForm.anyRole.length-1, 1)
          }
          break
        case 1:
          if (this.currentAnyPermission==null  ||  this.currentAnyPermission==''){
            return this.searchForm.anyPermission.splice(this.searchForm.anyPermission.length-1, 1)
          }
          break
        case 2:
          if (this.currentMustRole==null   ||  this.currentMustRole==''){
            return this.searchForm.mustRole.splice(this.searchForm.mustRole.length-1, 1)
          }
          break
        case 3:
          if (this.currentMustPermission==null   ||  this.currentMustPermission==''){
            return this.searchForm.mustPermission.splice(this.searchForm.mustPermission.length-1, 1)
          }
          break
        case 4:
          if  (this.currentLogicIndex==null){
            return this.searchForm.logicIndex.splice(this.searchForm.logicIndex.length-1, 1)
          }
      }
    },
    search(){
      AuthApi.add(this.searchForm).then(res=>{
        if (!res.data.code){
          this.$message.success(res.data.msg)
          this.axios.get(AuthApi.list, {
            params: {
              current: 1,
              pageSize: 10
            }
          }).then(res => {
            this.$parent.$parent.datas = res.data.data.records
          })
        }
      })
    }
  }
}
</script>

<style scoped>

</style>